<template>
  <div class="container">
    <div class="row justify-content-center">
      <Loading v-show="loading" />
      <div class="width mx-4">
        <div class="w-360">
          <p class="grayColor">請選擇喜歡的桌遊類型(多選)</p>
          <div class="row">
            <div class="col-6 px-3" v-for="Type in GameType" :key="Type">
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Type"
                  v-model="checkedGameTypes"
                  :value="Type"
                />
                <label class="form-check-label" :for="Type">{{ Type }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">請選擇喜歡的主題(多選)</p>
          <div class="row">
            <!-- v-for迴圈先bind Mechanisms to id，再bind value，由此可得個別單一值 -->
            <div
              class="col-6 px-3"
              v-for="Mechanisms in GameMechanisms"
              :key="Mechanisms"
            >
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Mechanisms"
                  v-model="checkedGameMechanisms"
                  :value="Mechanisms"
                />
                <label class="form-check-label" :for="Mechanisms">{{
                  Mechanisms
                }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">請選擇喜歡的遊戲機制(多選)</p>
          <div class="row">
            <div
              class="col-6 px-3"
              v-for="Category in GameCategory"
              :key="Category"
            >
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Category"
                  v-model="checkedCategories"
                  :value="Category"
                />
                <label class="form-check-label" :for="Category">{{
                  Category
                }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">以下幾款桌遊，你喜歡哪一款呢?(單選)</p>
          <div class="row">
            <div class="col-6 px-3" v-for="Game in GameName" :key="Game">
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="radio"
                  :id="Game.name"
                  :value="Game.name"
                  v-model="checkedGameName"
                />
                <label class="form-check-label" :for="Game.name">
                  {{ Game.name }}
                  <br /><img :src="Game.load" width="125" height="125" alt="" />
                </label>
              </div>
            </div>
          </div>
        </div>
        <router-link to="/stores">
          <div class="text-center my-3">
            <button class="mainBtn" @click="handleSubmit(userRegInfo)">
              註冊會員
            </button>
          </div>
        </router-link>
        <div class="toggle-form d-flex justify-content-center">
          <router-link to="login">已有會員？登入</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loading from "@/components/LoadingComponent.vue";
import { db } from "@/firebase/config";
import {
  // collection,
  doc,
  setDoc,
} from "firebase/firestore";
export default {
  components: { Loading },
  data() {
    return {
      checkedGameTypes: [],
      checkedGameMechanisms: [],
      checkedCategories: [],
      checkedGameName: "",

      loading: null,
      error: null,
      errorMsg: "",
      //   類型
      GameType: [
        "家庭遊戲",
        "主題遊戲",
        "派對遊戲",
        "深度策略",
        "棋奕遊戲",
        "輕度策略",
        "戰爭遊戲",
        "集換式遊戲",
      ],
      //   主題
      GameCategory: [
        "冒險",
        "驚悚",
        "戰爭",
        "奇幻",
        "動物",
        "幽默",
        "中世紀",
        "抽象",
        "經濟",
        "中國風",
        "諜報",
        "電玩改編",
      ],
      //機制
      GameMechanisms: [
        "合作",
        "角色扮演",
        "隱藏身分",
        "動作",
        "區域控制",
        "區域移動",
        "標記物放置",
        "版圖拼放",
        "棋盤",
        "賭博",
        "抽牌",
        "手牌控制",
        "卡片遊戲",
        "想像力",
        "投票",
        "競標",
        "判斷力",
        "決策力",
        "行動點控制",
        "骰子",
      ],
      // 名稱
      GameName: [
        {
          name: "詐賭巫師",
          load: require("../../assets/GameFavoritePng/詐賭巫師.png"),
        },
        {
          name: "矮人礦坑",
          load: require("../../assets/GameFavoritePng/矮人礦坑.png"),
        },
        {
          name: "農家樂",
          load: require("../../assets/GameFavoritePng/農家樂.png"),
        },
        {
          name: "富饒之城",
          load: require("../../assets/GameFavoritePng/榮耀之城.png"),
        },
        {
          name: "德古拉之怒",
          load: require("../../assets/GameFavoritePng/德古拉之怒.png"),
        },
        {
          name: "寶石陣",
          load: require("../../assets/GameFavoritePng/寶石陣.png"),
        },
        {
          name: "魔戒",
          load: require("../../assets/GameFavoritePng/魔戒.png"),
        },
        {
          name: "魔法風雲會",
          load: require("../../assets/GameFavoritePng/魔法風雲會.png"),
        },
      ],
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    userRegInfo() {
      // convert string back to object
      return JSON.parse(this.$route.params.userRegInfo);
    },
    userUid() {
      return this.$store.state.user.uid;
    },
  },
  methods: {
    getRandom(x) {
      return Math.floor(Math.random() * x) + 1;
    },
    getRandomID(ID) {
      var n = 0;
      for (var i = 0; i < 4; i++) {
        n = this.getRandom(9);
        ID += String(n);
      }
      this.bookingListId = ID;
      return ID;
    },
    //write data onto firebase
    async handleSubmit(userRegInfo) {
      let date = new Date();
      let dateResult = date.toDateString();
      await setDoc(doc(db, "Account", this.userUid), {
        //上一頁透過router傳過來的資料-userRegInfo
        PhotoURL:
          "https://firebasestorage.googleapis.com/v0/b/bgip-2205.appspot.com/o/Assets%2Fconfrontation.png?alt=media&token=ebbe1197-ec70-42a7-9525-7f7a94539c44",
        UserId: Number(this.getRandomID("1000")),
        Username: userRegInfo.username,
        Email: userRegInfo.email,
        Role: "一般用戶",
        Phone: userRegInfo.phone,
        Birthday: userRegInfo.birthday,
        Points: 0,
        Coupons: [],
        FavStores: [],
        FavJoins: [],
        FavArticles: [],
        RegisterDate: dateResult,
        PunishDate: "",
        Gender: userRegInfo.sex,

        //這頁要寫入的資料，用v-model binding
        //陣列內要給值才會有data
        GameCategory: this.checkedCategories,
        GameName: Array(this.checkedGameName),
        GameType: this.checkedGameTypes,
        GameMechanisms: this.checkedGameMechanisms,
      })
        .then(() => {
          this.loading = false;
          this.$router.push("/");
        })
        .catch((err) => {
          console.log(err.message);
          this.loading = false;
          this.$snackbar.add({
            type: "error",
            text: `${err.message}`,
          });
        });
    },
  },
};
</script>

<style scoped>
.width {
  max-width: 708px;
  min-width: 374px;
  background-color: rgb(255, 255, 255);
  margin-top: 8vh !important;
  margin-bottom: 8vh !important;
  padding: 6vh 6vw !important;
  border-radius: 12px;
  box-shadow: 0px 4px 4px 2px rgb(200, 200, 200);
  overflow: hidden; /* 切邊 */
  flex: auto;
  justify-content: center;
  align-items: center;
}
@media (min-width: 992px) {
}
@media (max-width: 400px) {
}
</style>
